import React, {
    useRef,
    useEffect,
    useCallback,
    useState,
    useMemo,
    forwardRef,
    useImperativeHandle
} from 'react';
import { MDCDialog } from '@material/dialog';
import { Icon } from '@element/react-icon';
import { IconButton } from '@element/react-icon-button';
import clsx from 'clsx';
import {
    addClass,
    addProps,
    returnFocusAfterClosing
} from '@element/react-common';
import { ModalPropTypes, ModalDefaultProps } from './generated/propTypes';

const Modal = forwardRef((props, externalRef) => {
    const {
        actionButton,
        actionIcons,
        badge,
        backButton,
        children,
        className,
        content,
        defaultButton,
        dismissiveButton,
        footerSupplemental,
        headerActions,
        hideCloseIcon,
        indicator,
        initialFocus,
        mobileStackedButtons,
        modalSize,
        nextButton,
        noActions,
        onClose,
        onClosed,
        onOpen,
        onOpened,
        open,
        preventClose,
        primaryButton,
        returnFocusSelector,
        scrollable,
        title,
        ...customProps
    } = props;

    const ref = useRef();
    const mdcDialog = useRef();

    useImperativeHandle(externalRef, () => ref.current, []);

    const [isMobile, setMobile] = useState();
    const [isDirectional, setIsDirectional] = useState(
        () => nextButton || backButton
    );

    useEffect(() => {
        setIsDirectional(nextButton || backButton);
    }, [nextButton, backButton]);

    const handleWindowSizeChange = useCallback(() => {
        setMobile(window.innerWidth <= 600);
    }, []);

    useEffect(() => {
        handleWindowSizeChange();
        window.addEventListener('resize', handleWindowSizeChange);

        return () => {
            window.removeEventListener('resize', handleWindowSizeChange);
        };
    }, [handleWindowSizeChange]);

    useEffect(() => {
        mdcDialog.current = MDCDialog.attachTo(ref.current);

        return () => {
            mdcDialog.current.destroy();
        };
    }, [mdcDialog, ref]);

    useEffect(() => {
        if (noActions) {
            mdcDialog.current.foundation.adapter.trapFocus = () => {};
        }
    }, [noActions]);

    useEffect(() => {
        mdcDialog.current.scrimClickAction = preventClose ? '' : 'close';
        mdcDialog.current.escapeKeyAction = preventClose ? '' : 'close';
    }, [preventClose]);

    const handleOnClose = useCallback(
        event => {
            onClose && onClose(event);
        },
        [onClose]
    );

    const handleOnClosed = useCallback(
        event => {
            returnFocusSelector && returnFocusAfterClosing(returnFocusSelector);
            onClosed && onClosed(event);
        },
        [returnFocusSelector, onClosed]
    );

    useEffect(() => {
        mdcDialog.current.unlisten('MDCDialog: opening', onOpen);
        mdcDialog.current.listen('MDCDialog: opening', onOpen);

        return () => {
            mdcDialog.current.unlisten('MDCDialog: opening', onOpen);
        };
    }, [mdcDialog, onOpen]);

    useEffect(() => {
        mdcDialog.current.unlisten('MDCDialog:opened', onOpened);
        mdcDialog.current.listen('MDCDialog:opened', onOpened);

        return () => {
            mdcDialog.current.unlisten('MDCDialog:opened', onOpened);
        };
    }, [mdcDialog, onOpened]);

    useEffect(() => {
        mdcDialog.current.unlisten('MDCDialog:closing', handleOnClose);
        mdcDialog.current.listen('MDCDialog:closing', handleOnClose);

        return () => {
            mdcDialog.current.unlisten('MDCDialog:closing', handleOnClose);
        };
    }, [mdcDialog, handleOnClose]);

    useEffect(() => {
        mdcDialog.current.unlisten('MDCDialog:closed', handleOnClosed);
        mdcDialog.current.listen('MDCDialog:closed', handleOnClosed);

        return () => {
            mdcDialog.current.unlisten('MDCDialog:closed', handleOnClosed);
        };
    }, [mdcDialog, handleOnClosed]);

    useEffect(() => {
        if (open) {
            mdcDialog.current.open();
        } else {
            mdcDialog.current.close();
        }
    }, [mdcDialog, open]);

    const actionButtonProps = useMemo(
        () => () => {
            const ret = {
                className: 'lmnt-modal__button mdc-dialog__button',
                elevation:
                    actionButton?.props.elevation !== 'default'
                        ? actionButton?.props.elevation
                        : 'flat',
                variant: actionButton?.props.variant || 'filled'
            };
            if (defaultButton === 'action') {
                ret['data-mdc-dialog-button-default'] = true;
            }
            if (initialFocus === 'action') {
                ret['data-mdc-dialog-initial-focus'] = true;
            }
            return ret;
        },
        [
            actionButton?.props.elevation,
            actionButton?.props.variant,
            defaultButton,
            initialFocus
        ]
    );

    const primaryButtonProps = useMemo(
        () => () => {
            const ret = {
                className: 'lmnt-modal__button mdc-dialog__button',
                'data-mdc-dialog-action': 'accept',
                elevation:
                    primaryButton?.props.elevation !== 'default'
                        ? primaryButton?.props.elevation
                        : 'flat',
                variant: primaryButton?.props.variant || 'filled'
            };
            if (defaultButton === 'primary') {
                ret['data-mdc-dialog-button-default'] = true;
            }
            if (initialFocus === 'primary') {
                ret['data-mdc-dialog-initial-focus'] = true;
            }
            return ret;
        },
        [
            defaultButton,
            initialFocus,
            primaryButton?.props.elevation,
            primaryButton?.props.variant
        ]
    );

    return (
        <aside
            ref={ref}
            className={clsx(
                'lmnt',
                'lmnt-modal',
                'mdc-dialog',
                open && 'mdc-dialog--open',
                scrollable && 'lmnt-modal--scrollable mdc-dialog--scrollable',
                className
            )}
            role='alertdialog'
            {...customProps}
        >
            <div
                className={clsx(
                    'lmnt-modal__container',
                    'mdc-dialog__container',
                    modalSize === 'fullscreen' && 'mdc-dialog--fullscreen',
                    modalSize === 'max' && 'lmnt-modal__max',
                    modalSize === 'xlarge' && 'lmnt-modal__xl',
                    modalSize === 'large' && 'lmnt-modal__lg',
                    modalSize === 'medium' && 'lmnt-modal__md',
                    modalSize === 'small' && 'lmnt-modal__sm',
                    (modalSize === 'mobile' || isMobile) &&
                        'lmnt-modal__mobile',
                    modalSize === 'dialog' && 'lmnt-modal__dialog',
                    noActions && 'lmnt-modal-no-actions'
                )}
            >
                <div className='lmnt-modal__surface mdc-dialog__surface'>
                    <header
                        className={clsx(
                            'lmnt-modal__title',
                            'mdc-dialog__title',
                            (modalSize === 'dialog' || hideCloseIcon) &&
                                'lmnt-modal_title-hide-close-icon',
                            actionIcons && 'lmnt-modal_title-actionsIcons',
                            scrollable && 'lmnt-modal__title-scrollable',
                            (hideCloseIcon ||
                                modalSize === 'dialog' ||
                                noActions) &&
                                'lmnt-modal--no-close-icon'
                        )}
                    >
                        {!hideCloseIcon &&
                            modalSize !== 'dialog' &&
                            !noActions && (
                                <IconButton
                                    className='mdc-icon-button material-icons mdc-dialog__close'
                                    data-mdc-dialog-action='close'
                                >
                                    <Icon icon='clear' />
                                </IconButton>
                            )}
                        {title && <div>{title}</div>}
                        {headerActions && (
                            <div className='lmnt-dialog__title-actions'>
                                {headerActions}
                            </div>
                        )}
                        {badge && (
                            <div
                                className='lmnt-badge--modal'
                                style={{ marginLeft: '0' }}
                            >
                                {badge}
                            </div>
                        )}
                    </header>

                    {(content || children) && (
                        <section className='lmnt-modal__content mdc-dialog__content'>
                            {content || children}
                        </section>
                    )}
                    {indicator}
                    {(actionButton ||
                        backButton ||
                        dismissiveButton ||
                        nextButton ||
                        primaryButton ||
                        footerSupplemental) && (
                        <footer
                            className={clsx(
                                'lmnt-modal__actions',
                                'mdc-dialog__actions',
                                isMobile && 'lmnt-dialog__actions__mobile',
                                footerSupplemental &&
                                    'lmnt-modal__actions--supplemental-btn',
                                isMobile &&
                                    mobileStackedButtons &&
                                    'lmnt-modal_actions-mobile-stacked'
                            )}
                        >
                            {!isDirectional && footerSupplemental && (
                                <div className='lmnt-dialog__actions__supplemental'>
                                    {addProps(footerSupplemental, () => {
                                        const ret = {};
                                        if (
                                            defaultButton ===
                                            'footerSupplemental'
                                        ) {
                                            ret[
                                                'data-mdc-dialog-button-default'
                                            ] = true;
                                        }
                                        if (
                                            initialFocus ===
                                            'footerSupplemental'
                                        ) {
                                            ret[
                                                'data-mdc-dialog-initial-focus'
                                            ] = true;
                                        }
                                        return ret;
                                    })}
                                </div>
                            )}

                            {!isDirectional &&
                                (actionButton ||
                                    dismissiveButton ||
                                    primaryButton) && (
                                    <div className='lmnt-dialog__actions__right-align lmnt-modal_actions_button'>
                                        {dismissiveButton &&
                                            addProps(dismissiveButton, {
                                                variant: 'text',
                                                className:
                                                    'lmnt-modal__button mdc-dialog__button',
                                                'data-mdc-dialog-action':
                                                    'close'
                                            })}
                                        {actionButton &&
                                            !primaryButton &&
                                            addProps(
                                                actionButton,
                                                actionButtonProps
                                            )}
                                        {primaryButton &&
                                            addProps(
                                                primaryButton,
                                                primaryButtonProps
                                            )}
                                    </div>
                                )}

                            {isDirectional && (
                                <div className='lmnt-modal_actions_button lmnt-dialog__actions__directional'>
                                    <div>
                                        {addClass(
                                            backButton,
                                            'lmnt-modal__button'
                                        )}
                                    </div>
                                    <div>
                                        {addProps(nextButton, {
                                            className: 'lmnt-modal__button',
                                            'data-mdc-dialog-initial-focus': true
                                        })}
                                    </div>
                                </div>
                            )}
                        </footer>
                    )}
                </div>
            </div>
            <div className='mdc-dialog__scrim' />
        </aside>
    );
});

Modal.displayName = 'Modal';
Modal.propTypes = ModalPropTypes;
Modal.defaultProps = ModalDefaultProps;

export default Modal;
